<template>
  <div class="box">
    <v-navbar current="download" />
    <b-overlay :show="showOverlay" rounded="sm" @shown="onShown" @hidden="onHidden">
      <template #overlay> <!-- Overlay -->
        <div class="d-block text-center">
          <b-spinner class="mx-auto"></b-spinner>
          <div v-if="!isLoaded" class="mx-auto">Please wait...</div>
          <div v-if="isLoaded && isProgress" class="mx-auto">
            <b-progress :value="progress" show-progress variant="success" max="100" class="progress">
            </b-progress>
          </div>
        </div>
      </template> <!-- Overlay -->
      <div class="d-block card-box m-24">
        <div class="card-title d-flex align-items-center">
          Download
        </div>

        <table>
          <thead>
            <tr>
              <td>&nbsp;</td>
              <td>1</td>
              <td>2</td>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>Slot</td>
              <td>{{slot1}}</td>
              <td>{{slot2}}</td>
            </tr>
            <tr>
              <td>Status</td>
              <td>{{labelStatus(status1)}}</td>
              <td>{{labelStatus(status2)}}</td>
            </tr>
            <tr>
              <td>Active</td>
              <td>{{(active1 === 0) ? 'FALSE' : 'TRUE'}}</td>
              <td>{{(active2 === 0) ? 'FALSE' : 'TRUE'}}</td>
            </tr>
            <tr>
              <td>Running</td>
              <td>{{(running1 === 0) ? 'FALSE' : 'TRUE'}}</td>
              <td>{{(running2 === 0) ? 'FALSE' : 'TRUE'}}</td>
            </tr>
            <tr>
              <td>File</td>
              <td>{{file1}}</td>
              <td>{{file2}}</td>
            </tr>
            <tr>
              <td>Ver</td>
              <td>{{ver1}}</td>
              <td>{{ver2}}</td>
            </tr>
          </tbody>
        </table>

        <div class="d-flex align-items-center mt-24 mb-16">
          <!-- <form class="m-0 p-0"> -->
            <label for="ex_filename" class="button ml-16">Choose file</label>
            <label for="ex_filename" class="input">{{file}}</label>
            <input ref="file" type="file" accept=".FGD, application/FGD" id="ex_filename" v-on:change="selsectFile">
            <button class="btn-default btn-download ml-16" v-on:click="upload">Download</button>
          <!-- </form> -->
        </div>
        <div class="body1 ml-16 mb-24">
          DK-RU-4CS3P4GSRU001MMmm.FGD<br>
          After reboot, the download file will be automatically applied to the inactive slot.
        </div>
      </div>
    </b-overlay>
    <!-- begin - popup for create a new user -->
    <b-modal id="error-modal" centered header-class="p-0 custom-modal-header" footer-class="p-0 custom-modal-footer" dialog-class="custom-modal-dialog2" content-class="custom-modal-content">
      <template #modal-header>
        <div class="modal-header">Alert</div>
      </template>
      
      <div class="d-block">
        <div class="d-flex justify-content-center">
          <img src="../assets/images/img-alert.png" class="mx-auto"/>
        </div>
        <div class="modal-desc">
          {{error}}
        </div>
      </div>

      <template #modal-footer>
        <div class="d-flex align-items-stretch modal-footer">
          <button @click="cancelReset()" class="flex-fill modal-btn cancel">
            Close
          </button>
        </div>
      </template>
    </b-modal>
    <!-- end - popup for create a new user -->
  </div>
</template>

<script>
import Navbar from '../components/Navbar.vue'
import axios from 'axios'

export default {
  name: 'system',
  data() {
    return {
      isLoaded: false,
      isProgress: false,
      polling: undefined,
      progress: 0,
      file: undefined,
      error: '',
      slot1: '',
      status1: '',
      active1: '',
      running1: '',
      file1: '',
      ver1: '',
      slot2: '',
      status2: '',
      active2: '',
      running2: '',
      file2: '',
      ver2: ''
    }
  },
  created() {
  },
  destroyed() {
  },
  computed: {
    showOverlay: function() {
      return !this.isLoaded || this.isProgress
    },
  },
  methods: {
    labelStatus(s) {
      if (s === 0) {
        return 'VALID'
      } else if (s === 1) {
        return 'INVALID'
      } else if (s === 2) {
        return 'EMPTY'
      }
      return 'UNKNOWN'
    },
    onShown() {
      if (this.isLoaded) {
        // TODO:
      } else {
        // 초기 데이터 로딩 시도
        this.loadInitData()
      }
    },
    onHidden() {
      this.progress = 0
    },
    loadInitData() {
      axios({
        method: 'get',
        url: this.$store.getters.apiServer + '/php/download.php',
        params: {
          key: this.$store.getters.sessionKey,
          id: this.$store.getters.userid
        },
      }).then((resp) => {
        if (resp.status == 200 && resp.data.state == 'Success') {
          const c = resp.data.data
          this.slot1 = c.slot1
          this.status1 = c.status1
          this.active1 = c.active1
          this.running1 = c.running1
          this.file1 = c.file1
          this.ver1 = c.ver1
          this.slot2 = c.slot2
          this.status2 = c.status2
          this.active2 = c.active2
          this.running2 = c.running2
          this.file2 = c.file2
          this.ver2 = c.ver2

          this.isLoaded = true
          if (!(this.status === 0 || this.status === 99)) {
            this.isProgress = true;
            this.polling = setInterval(this.loadDownloadStatus, 1000)
          }
        }
      }).catch(err => {
        console.log(err)
        setTimeout(() => {
          this.loadInitData()
        }, 1000)
      })
    },
    loadDownloadStatus() {
      axios({
        method: 'get',
        url: this.$store.getters.apiServer + '/php/download.php',
        params: {
          key: this.$store.getters.sessionKey,
          id: this.$store.getters.userid
        },
      }).then((resp) => {
        if (resp.status == 200 && resp.data.state == 'Success') {
          const c = resp.data.data
          this.slot1 = c.slot1
          this.status1 = c.status1
          this.active1 = c.active1
          this.running1 = c.running1
          this.file1 = c.file1
          this.ver1 = c.ver1
          this.slot2 = c.slot2
          this.status2 = c.status2
          this.active2 = c.active2
          this.running2 = c.running2
          this.file2 = c.file2
          this.ver2 = c.ver2

          if (c.reason !== 0) {
            this.isProgress = false
            clearInterval(this.polling)

            var reason = 'Unknown';
            switch(c.reason) {
              case 101:
                reason = "Checksum"
                break;
              case 102:
                reason = "Length"
                break;
              case 103:
                reason = "Abnormal"
                break;
              case 104:
                reason = "Timeout"
                break;
              case 105:
                reason = "No file"
                break;
              case 106:
                reason = "Missing file"
                break;
              case 107:
                reason = "Comm"
                break;
              case 108:
                reason = "Version mismatch"
                break;
              case 109:
                reason = "etc."
                break;
              case 110:
                reason = "Downloanding"
                break;
              case 111:
                reason = "Uploading"
                break;
              case 112:
                reason = "DU Downloading"
                break;
              case 113:
                reason = "AP"
                break;
              case 114:
                reason = "OS"
                break;
              case 115:
                reason = "BL"
                break;
              case 116:
                reason = "File name error"
                break;
              case 117:
                reason = "File name empty"
                break;
              case 118:
                reason = "Slot is same"
                break;
            }

            this.error = `Download failed ( ${reason} )`
            this.$bvModal.show('error-modal')  
          } else if (c.status === 0 || c.status === 99) {
            this.isProgress = false
            clearInterval(this.polling)
          }

          this.progress = c.progress
        }
      }).catch(err => {
        console.log(err)
        setTimeout(() => {
          this.loadInitData()
        }, 1000)
      })
    },
    selsectFile($event) {
      this.file = $event.target.files[0].name
    },
    upload() {
      if (this.$refs.file.files[0]) {
        this.isProgress = true  
        const frm = new FormData()
        frm.append('zipFile', this.$refs.file.files[0])

        axios({
          method: 'post',
          url: this.$store.getters.apiServer + '/php/download.php',
          params: {
            key: this.$store.getters.sessionKey
          },
          headers: {
            'Content-Type': 'multipart/form-data'
          },
          data: frm
        }).then(resp => {
          const c = resp.data.data
          if (c.status === 0 && c.reason === 111) {
            this.error = 'Upload failed'
            this.$bvModal.show('error-modal')  
          } else {
            this.isProgress = true;
            this.polling = setInterval(this.loadDownloadStatus, 1000)
          }
          this.file = ''
          this.$refs.file.value = ''
        }).catch(err => {
          this.file = ''
          this.$refs.file.value = ''
          this.error = err
          this.$bvModal.show('error-modal')
        })
      } else {
        this.error = 'Please select a file.'
        this.$bvModal.show('error-modal')
      }
    },
    cancelReset() {
      this.$bvModal.hide('error-modal')
    }
  },
  components: {
    'v-navbar': Navbar
  }
}
</script>
<style lang="scss" scoped>
@import "../scss/download.scss";
</style>